#research, #honors {
  padding: 50px 0px;
  text-align: justify;
  li {
    padding: 8px 0px;
    margin: 0px 40px;
  }
}

#references {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 50px 0px;
  min-height: 50vh;
  background-color: $mid-tone;
  @include back-highlighted("../image/back.jpg",
  -90deg,
  hsla(207, 63%, 60%, 0.7),
  hsla(207, 63%, 60%, 0)
  );
  .references-data {
    border-radius: 2%;
    width: 600px;
    text-align: center;
    background-color: $back-black;
    color: $front-white;
    padding: 20px;
    margin: auto auto;

    .reference-item {
      margin: 30px 0px;
    }
  }
}
